a:active, a:focus, a:hover, input[type="image"], select:focus, input, input[type="button"]:focus, :focus {
    outline: 0;
    text-decoration: none;
}
.clearfix {
    *zoom:1 
}
a {
    color: #000;
    transition: all 0.2s ease-in-out 0s;
    cursor: pointer;
}
a:hover {
    transition: all 0.2s ease-in-out 0s;
    color: #000;
}
p {
    margin-bottom: 15px;
}
img {
    max-width: 100%;
}
a:active, a:focus, a:hover, input[type="image"], select:focus, input, input[type="button"]:focus, :focus {
    outline: 0;
    text-decoration: none;
    transition: 0.5s all;
    -webkit-transition: 0.5s all;
    -moz-transition: 0.5s all;
    -o-transition: 0.5s all;
    -ms-transition: 0.5s all;
}
.clearfix:before, .clearfix:after {
    display: block;
    content: "";
    line-height: 0 
}
.clearfix:after {
    clear: both;
}
.clear {
    clear: both 
}
.btn {
    outline: 0px;
}
.lists {
    list-style: none;
    margin: 0px;
    padding: 0px;
}
input {
    border-radius: 0 
}
.form-control {
    border-radius: 0;
    height: 46px;
}
