/* Select Dropdown Css */
/* Select Dropdown Css */
.select_box {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
}
.select_box select {
    -moz-appearance: none;
    color: #999;
    background: none;
    -moz-appearance: none;
    appearance: none;
    -webkit-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    cursor: pointer;
    float: left;
    font-size: 14px;
    overflow: hidden;
    padding-left: 10px;
    height: 46px;
    text-overflow: ellipsis;
    white-space: normal;
    width: 100%;
    border: 1px solid #dddddd;
    border-radius: 0px;
    background: #ffffff;
    width: 100%;
}
.select_box span::after {
    background: url(../images/select_arrow.png) no-repeat top right;
    bottom: 0;
    color: #333;
    content: "";
    height: 10px;
    padding: 14px 25px;
    pointer-events: none;
    position: absolute;
    right: 15px;
    top: 20px;
    z-index: 1;
}
.select_box span {
    display: inline-block;
    float: left;
    overflow: hidden;
    position: relative;
    width: 100%;
}
