    width: 50px;
    height: 1px;
    left: 25%;
    top: 17px;
}
.left_logincontent .or:after {
    content: "";
    position: absolute;
    background-color: #C3C6C8;
    display: block;
    width: 50px;
    height: 1px;
    right: 25%;
    top: 17px;
}
